import React, {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {useHistory, useParams} from 'react-router-dom';


import {getDetalles, reloadDetalles} from '../../actions/index';
import RecipeDetails from '../RecipeDetails';


export default function Detail() {
    const dispatch = useDispatch();
    const history = useHistory();
    const {id} = useParams();

    useEffect(() => {
        dispatch(getDetalles(id))
        return () => {
            dispatch(reloadDetalles())
        }
    }, [dispatch, id]);

    // useEffect(() => {
    //     dispatch(getDetail(props.match.params.id))
    // }, [props.match.params.id,dispatch])

    const detail = useSelector((state) => state.detalles);


    console.log(detail[0])
    
    // console.log("Diets: ", detail.diets);

    function handle_button_home(e){
        e.preventDefault();
        dispatch(reloadDetalles());
        history.push('/home');
    }

    // const instructions = detail[0].pasoAPaso.map(instruction => {
    //     return instruction.steps.map(step => step.step)
    // }).flat()
    
    // console.log(instructions)
    return (
        <div>
            <button onClick={handle_button_home}>Volver</button>
            {
                Object.keys(detail).length > 0 ? 
                <div >
                    <div >
                        <img src={detail[0].imagen} alt={"imagen no encotrada"}  />
                    </div>
                    <div >
                        <li>
                        <RecipeDetails name="Id" data={detail[0].id}  />
                        </li>
                        <li><RecipeDetails name="Nombre" data={detail[0].nombre ? detail[0].nombre : detail[0].nombre} /></li>
                        <li><RecipeDetails name="Dietas" data={Array.isArray(detail[0].dieta) ? detail[0].dieta.map(d => d.name ? <label> {d.name} * </label> : 
                        <label> {d} * </label>) : detail[0].dieta } /></li>
                        <li><RecipeDetails name="Resumen" data={detail[0].resumen} /></li>
                        <li><RecipeDetails name="Puntuacion" data={detail[0].puntuacion} /></li>
                        <li><RecipeDetails name="Nivel de comida saludable" data={detail[0].nivel_de_comida_saludable} /></li>
                        <li><RecipeDetails name="Instrucciones" data={detail[0].paso_a_paso} /></li>
                    </div>
                </div>
                : <p>Espere..</p>
            }
        </div>
    )
}