import React from "react";

import {useHistory} from 'react-router-dom'


export default function Card({nombre, imagen, dieta, id, puntuacion,}){

    const history = useHistory();

    function handle_click(e){
        e.preventDefault();
        history.push(`/detalles/${id}`);
    }

    return (
        <div>
            <img  src={imagen} alt={"No Se encontro la imagen"} />
            <div>
                <label>{nombre}</label>
            </div>
            <div>
                <label>puntuacion:{puntuacion}</label>
            </div>
            <div>
                <h4>Tipo de dieta</h4>
            {
            Array.isArray(dieta) ? dieta.map(d=> d.name? <label >{d.name}</label> :<label>{d} </label>) : ""
               }
                
            </div>
            <button  onClick={handle_click}>Ver MÃ¡s</button>
        </div>
    )
}
