import React from 'react';
import { useHistory } from 'react-router-dom';


function Landing() {
    const history = useHistory();

    function handle_home(e) {
        e.preventDefault();
        history.push("/home");
    }
    return (
        <div >
            <div>
                <label>RECETARIO ONLINE</label>
            </div>
            <div>
                <button onClick={handle_home}>Inicio</button>
            </div>
        </div>
    )
}

export default Landing;